import React, { useEffect, useState } from 'react'
import { Link, NavLink } from 'react-router-dom'
// import VisibilityIcon from '@mui/icons-material/Visibility';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import BorderColorIcon from '@mui/icons-material/BorderColor';
import DeleteIcon from '@mui/icons-material/Delete';
import axios from 'axios';

export default function Home() {
  const [getuserdata, setUserdata] = useState([])


  const getData = async (e)=>{

    const res = await axios.get("/getdata",{
      headers:{
        "Content-Type":"application/json"
      }
    })

    const data = await res.data;
    console.log(data)

    if(res.status === 422 || !data){
      console.log("error")
    }else{
      setUserdata(data)
      console.log("get data")
    }
}

useEffect(()=>{
  getData();
},[])

const deleteUser = async(id)=>{
  const res2 = await axios.delete(`/deleteuser/${id}`,{
    headers: {
      "Content-Type":"application/json"
    }
  })

  const deleteData = await res2.data;
  console.log(deleteData)

  if(res2.status ===422 || !deleteData){
    console.log("error")
  }else{
    console.log("user deleted")
    getData();
  }

}

  return (
    <>
    <div className="mt-5">
        <div className="container">
            <div className="add_btn mt-2">
                <Link to="/register"><button className="btn btn-primary">Add Data</button></Link>
            </div>
            <table class="table">
  <thead>
    <tr className='table-dark'>
      <th scope="col">ID</th>
      <th scope="col">Username</th>
      <th scope="col">email</th>
      <th scope="col">Job</th>
      <th scope="col">Number</th>
      <th scope="col">Functions</th>
    </tr>
  </thead>
  <tbody>
    {
      getuserdata.map((element, id)=>{
        return(
          <>
          <tr>
      <th scope="row">{id+1}</th>
      <td>{element.name}</td>
      <td>{element.email}</td>
      <td>{element.work}</td>
      <td>{element.mobile}</td>
      <td className='d-flex justify-content-between'>
        <NavLink to={`view/${element._id}`}><button><RemoveRedEyeIcon/></button></NavLink>
        <Link to={`edit/${element._id}`} ><button><BorderColorIcon/></button></Link>
        <button onClick={()=>deleteUser(element._id)}><DeleteIcon/></button>
      </td>
    </tr>
          </>
        )
      })
    }

    
    
  </tbody>
</table>
        </div>
    </div>
    </>
  )
}
