import React, { useEffect, useState } from 'react'
import Card from '@mui/material/Card';
// import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import MailIcon from '@mui/icons-material/Mail';
import WorkIcon from '@mui/icons-material/Work';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import CreateIcon from '@mui/icons-material/Create';
import DeleteIcon from '@mui/icons-material/Delete';
import { NavLink, useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';


export default function Details() {

  const navigate = useNavigate()
  const [getuserdata, setUserdata] = useState([])

  const {id} = useParams("");
  console.log(id)

  const getData = async ()=>{

    const res = await fetch(`/getuser/${id}`,{
      method: "GET",
      headers:{
        "Content-Type":"application/json"
      }
    })

    const data = await res.json()
    console.log(data)

    if(res.status === 422 || !data){
      console.log("error")
    }else{
      setUserdata(data)
      console.log("get data")
    }
}

useEffect(()=>{
  getData()
},[])

const deleteUser = async(id)=>{
  const res2 = await axios.delete(`/deleteuser/${id}`,{
    headers: {
      "Content-Type":"application/json"
    }
  })

  const deleteData = await res2.data;
  console.log(deleteData)

  if(res2.status ===422 || !deleteData){
    console.log("error")
  }else{
    console.log("user deleted")
    navigate("/")
  }

}

  return (
    <>
    <div className="container mt-3">
        <h1>Welcome {getuserdata.name}</h1>

        <Card sx={{ maxWidth: 800 }}>
      <CardContent>
        <div className="row">
        <div className="left_view col-lg-6 col-md-6 col-12">
            <img src="https://cdn-icons-png.flaticon.com/512/3135/3135715.png" alt="profile" style={{width: 50}} />
            <h3 className="mt-3">Name: <span>{getuserdata.name}</span></h3>
            <h3 className="mt-3">age: <span>{getuserdata.age}</span></h3>
            <p><MailIcon/>Email: <span>{getuserdata.email}</span></p>
            <p><WorkIcon/>Occupation: <span>{getuserdata.work}</span></p>
        </div>
        <div className="right_view col-lg-6 col-md-6 col-12">
            <div className="add_btn">
                <NavLink to={`/edit/${getuserdata._id}`}><div className="btn btn-primary mx-2"><CreateIcon/></div></NavLink>
                <button className="btn btn-danger" onClick={()=>deleteUser(getuserdata._id)}><DeleteIcon/></button>

            </div>
            <p><LocalPhoneIcon/> mobile: <span>{getuserdata.mobile}</span></p>
            <p><LocationOnIcon/> Location: <span>{getuserdata.add}</span></p>
            <p>Description: <span>{getuserdata.desc}</span></p>
        </div>
        </div>
        
      </CardContent>
      </Card>
    </div>
    </>
  )
}
