import React,{useState} from 'react'
import { useNavigate } from 'react-router-dom';

export default function Register() {
  
  const navigate = useNavigate()

    const [val, setVal] = useState({
        name:"",
        email:"",
        age:"",
        mobile:"",
        work:"",
        add:"",
        desc:""
    })

    const setdata =(e)=>{
        console.log(e.target.value);
        const { name, value } = e.target;
        setVal((preval) => {
            return {
                ...preval,
                [name]: value
            }
        })
    }

    const addData = async (e)=>{
        e.preventDefault();

        const {name,email,age,mobile,work,add,desc} = val;
        const res = await fetch("/register",{
          method: "POST",
          headers:{
            "Content-Type":"application/json"
          },
          body: JSON.stringify({
            name,email,age,mobile,work,add,desc
          })
        })

        const data = await res.json()
        console.log(data)

        if(res.status === 422 || !data){
          alert("error")
          console.log("error")
        }else{
          alert("data added")
          console.log("data added")
          navigate("/")
        }
    }

  return (
    <>
    <form>
        <div className="row">
  <div className="mb-3 col-lg-6 col-md-6 col-12">
    <label for="exampleInputEmail1" className="form-label">Name</label>
    <input type="text" className="form-control" name="name" onChange={setdata} value={val.name} id="exampleInputEmail1"   aria-describedby="emailHelp"/>
  </div>
  <div className="mb-3 col-lg-6 col-md-6 col-12">
    <label for="exampleInputEmail1" className="form-label">email</label>
    <input type="email" className="form-control" name ="email" onChange={setdata} value={val.email} id="exampleInputEmail1" aria-describedby="emailHelp"/>
  </div>
  <div className="mb-3 col-lg-6 col-md-6 col-12">
    <label for="exampleInputEmail1" className="form-label">age</label>
    <input type="number" className="form-control" name="age" onChange={setdata} value={val.age} id="exampleInputEmail1" aria-describedby="emailHelp"/>
  </div>
  <div className="mb-3 col-lg-6 col-md-6 col-12">
    <label for="exampleInputEmail1" className="form-label">mobile</label>
    <input type="number" className="form-control" name="mobile" onChange={setdata} value={val.mobile} id="exampleInputEmail1" aria-describedby="emailHelp"/>
  </div>
  <div className="mb-3 col-lg-6 col-md-6 col-12">
    <label for="exampleInputEmail1" className="form-label">work</label>
    <input type="text" className="form-control" name="work" onChange={setdata} value={val.work} id="exampleInputEmail1"  aria-describedby="emailHelp"/>
  </div>
  <div className="mb-3 col-lg-6 col-md-6 col-12">
    <label for="exampleInputEmail1" className="form-label">Adress</label>
    <input type="text" className="form-control" name="add" onChange={setdata} value={val.add} id="exampleInputEmail1" aria-describedby="emailHelp"/>
  </div>
  <div className="mb-3">
    <label for="exampleInputEmail1" className="form-label">discription</label>
    <textarea name="desc" onChange={setdata} value={val.desc} className='form-control' id="" cols="30" rows="10"></textarea>
  </div>
  <div className="mb-3 form-check">
  </div>
  <button onClick={addData}  type="submit" className="btn btn-primary">Submit</button>
  </div>
</form>
    </>
  )
}
